	module.exports = {"data":{"post":{"html":"<p>We seem to already be in vacation mode, although we don't leave until Saturday.  It's not hard when it's 10&deg;C and rainy here, while it's 35&deg;C in Las Vegas.</p>\n<p>I've been trying out <a href=\"http://www.activestate.com/Products/Komodo/\">ActiveState Komodo</a>, an IDE/debugger for Perl and PHP, among others.  (For my non-programming family and friends, it's an editor for writing programs that can also help find mistakes.) I found it helpful yesterday afternoon when I was alone with Christopher, since I was able to easily find my place after the frequent interruptions.</p>","fields":{"title":"Komodo","date":"15 Apr 2002","url":"https://papascott.de/2002-04-15-komodo/"}}},"pathContext":{"slug":"/2002-04-15-komodo/","prev":{"fields":{"title":"Germany in the Spring and Jiang Zemin","date":"12 Apr 2002","slug":"/2002-04-12-germany-in-the-spring-and-jiang-zemin/"}},"next":{"fields":{"title":"285 mpg","date":"16 Apr 2002","slug":"/2002-04-16-285-mpg/"}}}}

